import React from 'react';
import { useSelector } from 'react-redux';
import './cartDropdown.css';
import { Button } from 'reactstrap';
import { Link } from 'react-router-dom';

const CartDropdown = () => {
    const publicUrl = process.env.PUBLIC_URL;
    const cartItems = useSelector((state) => state.cart.items);
    const cartCount = cartItems.reduce((total, item) => total + item.quantity, 0);

    // Tnh tng gi tin, m bo khng c li NaN do kiu d liu khng ng
    const totalPrice = cartItems.reduce((total, item) => {
        // Chuyn gi t chui thnh s, nu khng chuyn i c s tr v 0
        const price = parseFloat(item.price.replace(/,/g, '')) || 0;
        // Chuyn s lng t chui thnh s nguyn, nu khng chuyn i c s tr v 0
        const quantity = parseInt(item.quantity, 10) || 0;
        return total + price * quantity;
    }, 0);

    return (
        <div className='cart-dropdown'>
            <div className='cart-header'>
                <h4>Gi hng ({cartCount})</h4>
            </div>
            <div className='cart-body'>
                {cartItems.length === 0 ? (
                    <p>Gi hng ca bn ang trng</p>
                ) : (
                    cartItems.map((item) => (
                        <div key={item.id} className='cart-item'>
                            <img src={`${publicUrl}/images/products/${item.image}.jpg`} alt={item.name} />
                            <div className='cart-item-info'>
                                <p className='item-name'>{item.name}</p>
                                <div className='bg-content'>
                                    <p className='quantity'>S lng: {item.quantity}</p>
                                    <p className='price'>{parseFloat(item.price.replace(/,/g, '')).toLocaleString()} VND</p>
                                </div>
                            </div>
                        </div>
                    ))
                )}
            </div>
            <div className='cart-footer'>
                <p className='totalmoney'>Tng tin: <span className='res'>{totalPrice.toLocaleString()} VND</span></p>
                <Link to="/cart-detail"><Button>Xem gi hng</Button></Link>
            </div>
        </div>
    );
};

export default CartDropdown;
